let timer = null;
export default {
  methods: {
    generateImg() {
      let div = this.$refs.div;
      let i = Math.floor(Math.random() * 8) + 1;
      let img = document.createElement("img");
      img.src = require('../assets/images/' + i + '.png');
      img.width = 25;
      img.height = 25;
      div.appendChild(img);
    }
  },
  beforeCreate() {
    //此時剛初始化vue實體，尚未建立vue實體，所以此實體的設定尚未配置。
    //在vue3已被setup()取代。
    console.log("Before Create");
  },
  created() {
    //建立vue實體了，此時除了$el外，皆已配置，因為還沒將實體渲染進頁面。
    //在vue3已被setup()取代。
    console.log("Create");
  },
  setup() {
    //在vue3，setup()等同beforeCreate()+created()，但上述2函式還是可以用。
    console.log("setup");
  },
  mounted() {
    console.log("mounted");
    timer = setInterval(() => {
      this.generateImg();
    }, 500);
  },
  updated() {
    console.log("updated");
  },
  beforeUnmount() {
    console.log("Before unmount");
    clearInterval(timer);
    timer = null;
  },
  unmounted() {
    console.log("unmounted");
  }
};