import { ref } from 'vue';
const bool = ref(false);
export default {
  data() {
    return {
      bool
    };
  },
  watch: {
    bool: function (newValue) {
      switch_to_dark(newValue);
    }
  }
};
function switch_to_dark(flag) {
  let html_el = document.getElementsByTagName("html")[0];
  if (flag) {
    html_el.classList.add("dark");
  } else {
    html_el.classList.remove("dark");
  }
}